.appointment {
  padding: 13px 18px 49px;
  margin: 0 auto;

  & br {
    display: none;
  }

  @media (min-width: $tablet-width) {
    padding: 61px 134px 58px;
  }

  @media (min-width: $desktop-width) {
    padding: 61px 300px 58px;
  }
}

.appointment__title {
  font-weight: 700;
  font-size: 21px;
  line-height: 1.24;
  text-align: center;
  text-transform: uppercase;
  margin: 0 0 20px;

  @media (min-width: $tablet-width) {
    font-size: 30px;
    line-height: 0.87;
    margin: 0 auto 29px;
  }
}

.appointment__text {
  color: $black-lighter;
  text-align: center;
  text-transform: uppercase;
  margin: 0 0 32px;

  @media (min-width: $tablet-width) {
    margin: 0 auto 42px;
  }

  @media (min-width: $desktop-width) {
    margin: 0 auto 47px;
  }
}

.appointment__link {
  color: $white;
  background-color: $blue;
  padding: 16px 10px;
  display: flex;
  justify-content: center;
  max-width: 100%;
  font-weight: 700;
  font-size: 21px;
  line-height: 1.24;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    width: 442px;
    margin: 0 auto;
  }

  &:hover {
    background-color: $blue-hover;
  }

  &:active {
    background-color: $blue-active;
    color: rgba(255, 255, 255, 0.3);
  }

  &:disabled {
    background-color: $grey;
  }
}
